:root {
  --primary: #2d5016;
  --primary-light: #4a7c59;
  --accent: #6b8f47;
  --success: #4a7c59;
  --danger: #dc3545;
  --warning: #f59e0b;
  --urgent: #ff6b6b;
  --text-primary: #1a1a1a;
  --text-secondary: #666666;
  --text-muted: #999999;
  --border: #e8e8e8;
  --border-light: #f5f5f5;
  --bg-main: #ffffff;
  --bg-secondary: #fafafa;
  --bg-accent: #f8fbf6;
  --shadow-sm: 0 1px 3px 0 rgb(0 0 0 / 0.05);
  --shadow: 0 4px 12px 0 rgb(0 0 0 / 0.05);
  --shadow-lg: 0 20px 40px 0 rgb(0 0 0 / 0.08);
  --radius: 8px;
  --radius-lg: 16px;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: linear-gradient(135deg, #fafafa 0%, #f5f7f5 100%);
  color: var(--text-primary);
  line-height: 1.7;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background: linear-gradient(135deg, #fafafa 0%, #f8fbf6 50%, #f5f7f5 100%);
}

.auth-container {
  background: var(--bg-main);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  padding: 4rem 3rem;
  width: 100%;
  max-width: 480px;
  border: 1px solid var(--border-light);
  position: relative;
  overflow: hidden;
}

.auth-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary) 0%, var(--accent) 100%);
}

.auth-header h1 {
  text-align: center;
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 2.25rem;
  font-weight: 300;
  letter-spacing: -0.02em;
}

.auth-header h1::after {
  content: '';
  display: block;
  width: 60px;
  height: 2px;
  background: var(--accent);
  margin: 1.5rem auto 3rem;
}

.auth-tabs {
  display: flex;
  margin-bottom: 3rem;
  background: var(--bg-secondary);
  border-radius: var(--radius);
  padding: 6px;
  position: relative;
}

.auth-tabs button {
  flex: 1;
  padding: 1rem 1.5rem;
  border: none;
  background: transparent;
  color: var(--text-secondary);
  cursor: pointer;
  transition: var(--transition);
  font-weight: 500;
  font-size: 0.95rem;
  border-radius: calc(var(--radius) - 6px);
  position: relative;
  z-index: 1;
}

.auth-tabs button.active {
  background: var(--bg-main);
  color: var(--primary);
  box-shadow: var(--shadow-sm);
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.input-group {
  position: relative;
  display: flex;
  align-items: center;
  border: 2px solid var(--border);
  border-radius: var(--radius);
  padding: 1.25rem 1.5rem;
  transition: var(--transition);
  background: var(--bg-main);
}

.input-group:focus-within {
  border-color: var(--accent);
  box-shadow: 0 0 0 4px rgb(107 143 71 / 0.1);
}

.input-group svg {
  color: var(--text-muted);
  margin-right: 1rem;
  flex-shrink: 0;
}

.input-group input {
  border: none;
  outline: none;
  flex: 1;
  font-size: 1rem;
  color: var(--text-primary);
  background: transparent;
  font-weight: 400;
}

.input-group input::placeholder {
  color: var(--text-muted);
  font-weight: 400;
}

.submit-btn {
  background: var(--primary);
  color: white;
  border: none;
  border-radius: var(--radius);
  padding: 1.25rem 2rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
  margin-top: 1rem;
  letter-spacing: 0.025em;
}

.submit-btn:hover {
  background: var(--primary-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.submit-btn:active {
  transform: translateY(0);
}

.error-message {
  background: rgb(254 242 242);
  color: var(--danger);
  padding: 1rem 1.5rem;
  border-radius: var(--radius);
  font-size: 0.9rem;
  border: 1px solid rgb(254 202 202);
  font-weight: 500;
}

.success-message {
  background: var(--bg-accent);
  color: var(--success);
  padding: 1rem 1.5rem;
  border-radius: var(--radius);
  font-size: 0.9rem;
  border: 1px solid #c6f6d5;
  font-weight: 500;
}

.task-container {
  background: var(--bg-main);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  padding: 4rem;
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  border: 1px solid var(--border-light);
  position: relative;
}

.task-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary) 0%, var(--accent) 100%);
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3rem;
  padding-bottom: 2rem;
  border-bottom: 2px solid var(--border-light);
}

.task-header h1 {
  color: var(--text-primary);
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: -0.02em;
}

.task-stats {
  display: flex;
  gap: 1rem;
  margin-top: 0.5rem;
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.email-indicator {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--bg-accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius);
  border: 1px solid var(--success);
}

.email-indicator svg {
  color: var(--success);
}

.email-indicator span {
  font-size: 0.8rem;
  color: var(--success);
  font-weight: 500;
}

.logout-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: var(--bg-secondary);
  color: var(--text-secondary);
  border: 2px solid var(--border);
  border-radius: var(--radius);
  padding: 0.75rem 1.5rem;
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.9rem;
  font-weight: 500;
}

.logout-btn:hover {
  background: var(--danger);
  color: white;
  border-color: var(--danger);
  transform: translateY(-1px);
}

.add-task-section h2 {
  color: var(--text-primary);
  margin-bottom: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: -0.02em;
}

.add-task-form {
  display: grid;
  grid-template-columns: 1fr auto auto;
  gap: 1rem;
  margin-bottom: 3rem;
  padding: 2rem;
  background: var(--bg-accent);
  border-radius: var(--radius);
  border: 2px solid var(--border-light);
}

.task-input {
  grid-column: 1;
  border: 2px solid var(--border);
  border-radius: var(--radius);
  padding: 1rem 1.5rem;
  font-size: 1rem;
  outline: none;
  transition: var(--transition);
  background: var(--bg-main);
  font-weight: 400;
}

.task-input:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 4px rgb(107 143 71 / 0.1);
}

.date-input {
  grid-column: 2;
  border: 2px solid var(--border);
  border-radius: var(--radius);
  padding: 1rem 1.5rem;
  font-size: 1rem;
  outline: none;
  transition: var(--transition);
  background: var(--bg-main);
  color: var(--text-primary);
  min-width: 160px;
}

.date-input:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 4px rgb(107 143 71 / 0.1);
}

.add-btn {
  grid-column: 3;
  display: inline-flex;
  align-items: center;
  gap: 0.75rem;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: var(--radius);
  padding: 1rem 2rem;
  cursor: pointer;
  transition: var(--transition);
  font-weight: 600;
  font-size: 1rem;
  white-space: nowrap;
}

.add-btn:hover {
  background: var(--primary-light);
  transform: translateY(-2px);
  box-shadow: var(--shadow);
}

.task-list-header {
  margin-bottom: 2rem;
}

.section-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section-title h2 {
  color: var(--text-primary);
  font-size: 1.5rem;
  font-weight: 400;
  letter-spacing: -0.02em;
}

.setup-email-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--success);
  color: white;
  border: none;
  border-radius: var(--radius);
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.85rem;
  font-weight: 500;
}

.setup-email-btn:hover {
  background: var(--primary);
  transform: translateY(-1px);
}

.send-emails-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  background: var(--accent);
  color: white;
  border: none;
  border-radius: var(--radius);
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.85rem;
  font-weight: 500;
}

.send-emails-btn:hover {
  background: var(--primary);
  transform: translateY(-1px);
}

.tasks-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.no-tasks {
  text-align: center;
  color: var(--text-muted);
  font-style: italic;
  padding: 4rem 2rem;
  background: var(--bg-secondary);
  border-radius: var(--radius);
  border: 2px dashed var(--border);
  font-size: 1.1rem;
}

.no-tasks svg {
  margin: 0 auto 1rem;
  opacity: 0.5;
}

.task-item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
  background: var(--bg-main);
  border: 2px solid var(--border-light);
  border-radius: var(--radius);
  transition: var(--transition);
  position: relative;
  animation: fadeIn 0.4s ease-out;
}

.task-item:hover {
  border-color: var(--accent);
  box-shadow: var(--shadow);
  transform: translateY(-2px);
}

.task-item.completed {
  background: var(--bg-accent);
  border-color: var(--success);
}

.task-item.urgent {
  border-left: 6px solid var(--urgent);
  background: linear-gradient(90deg, #fff5f5 0%, var(--bg-main) 20%);
}

.task-item.due-soon {
  border-left: 6px solid var(--warning);
  background: linear-gradient(90deg, #fffbf0 0%, var(--bg-main) 20%);
}

.task-item.overdue {
  border-left: 6px solid var(--danger);
  background: linear-gradient(90deg, #fef2f2 0%, var(--bg-main) 20%);
}

.task-item.completed .task-text {
  text-decoration: line-through;
  color: var(--text-muted);
}

.toggle-btn {
  background: transparent;
  border: 3px solid var(--border);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: var(--transition);
  color: var(--text-muted);
  flex-shrink: 0;
}

.task-item.completed .toggle-btn {
  background: var(--success);
  border-color: var(--success);
  color: white;
}

.toggle-btn:hover {
  border-color: var(--accent);
  color: var(--accent);
  transform: scale(1.1);
}

.task-content {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  flex: 1;
}

.task-text {
  font-size: 1.1rem;
  color: var(--text-primary);
  font-weight: 500;
  line-height: 1.4;
}

.task-date {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 400;
}

.task-date svg {
  margin-right: 0.5rem;
}

.task-urgent {
  color: var(--urgent);
  font-weight: 600;
}

.task-due-soon {
  color: var(--warning);
  font-weight: 600;
}

.task-overdue {
  color: var(--danger);
  font-weight: 600;
}

.delete-btn {
  background: transparent;
  color: var(--text-muted);
  border: none;
  border-radius: var(--radius);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: var(--transition);
  flex-shrink: 0;
}

.delete-btn:hover {
  background: var(--danger);
  color: white;
  transform: scale(1.1);
}

.email-config {
  margin-top: 2rem;
  text-align: center;
  color: var(--text-muted);
  font-size: 0.9rem;
  padding: 1rem;
  background: var(--bg-accent);
  border-radius: var(--radius);
  border: 1px solid var(--border-light);
}

.change-email-btn {
  margin-left: 1rem;
  background: none;
  border: none;
  color: var(--accent);
  cursor: pointer;
  text-decoration: underline;
  font-size: 0.9rem;
}

.change-email-btn:hover {
  color: var(--primary);
}

@media (max-width: 768px) {
  .app {
    padding: 1rem;
  }
  
  .auth-container,
  .task-container {
    padding: 2rem;
  }
  
  .auth-header h1 {
    font-size: 1.75rem;
  }
  
  .task-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .add-task-form {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .task-input,
  .date-input,
  .add-btn {
    grid-column: 1;
  }
  
  .task-item {
    grid-template-columns: auto 1fr auto;
    gap: 1rem;
  }
  
  .section-title {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.task-container::-webkit-scrollbar {
  width: 8px;
}

.task-container::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 4px;
}

.task-container::-webkit-scrollbar-thumb {
  background: var(--border);
  border-radius: 4px;
}

.task-container::-webkit-scrollbar-thumb:hover {
  background: var(--text-muted);
}